body {
	font: 14px "Century Gothic", Futura, sans-serif;
	margin: 20px;
}

ol,
ul {
	padding-left: 30px;
}

@font-face {
  font-family: react;
  src: url(./oswald/Oswald-Regular.ttf);
}
.react {
  font-family: react;
  font-size: 22px;
  font-weight: 300;
}

.title {
	margin-bottom: 30px;
}

.board-row:after {
	clear: both;
	content: "";
	display: table;
}

.status {
	margin-bottom: 10px;
}

.square {
	background: #fff;
	border: 1px solid #999;
	float: left;
	font-size: 24px;
	font-weight: bold;
	line-height: 64px;
	height: 64px;
	margin-right: -1px;
	margin-top: -2px;
	padding: 0;
	text-align: center;
	width: 64px;
}

.square:focus {
	outline: none;
}

.kbd-navigation .square:focus {
	background: #ddd;
}

.game {
	display: flex;
	flex-direction: row;
}

.game-info {
	margin-left: 20px;
}

.timeline:hover {
	background-color: antiquewhite;
}

@keyframes flash {
	from{background-color: white;}
	to{background-color: yellow;}
}